.App {
  text-align: center;
  background-color: #fffff0;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #c59f9b;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 6vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.split-holder {
  display: flex;
  justify-content: space-between;
  max-width: 100%;
  max-height: 100%;
  flex-wrap: wrap;
}

.nameOutput {
  font-size: 10vw;
  font-size: 10vh;
  max-width: 100%;
  word-wrap: break-word;
  margin-top: 20px;
}

.split-left {
  width: 25vw;
  height: 100vh;
}

.split-right {
  max-width: 65vw;
  width: 65vw;
  display: flex;
  flex-wrap: wrap;
  padding-right: 30px;
  align-content: flex-start;
}

input {
  /* background: linear-gradient(21deg, #10abff, #1beabd); */
  border-radius: 9999em;
  padding: 2px;
  margin: 3px;
  display: inline-block;
  width: 100%;
  height: 4vh;
  text-align: center;
}

.css-1s2u09g-control {
  margin: 3px;
  margin-left: 14px;
}

@media screen and (max-width: 900px) {
  input {
    width: 90%;
  }

  .css-b62m3t-container {
    width: 95%;
    margin-left: 5px;
  }

  .split-left {
    width: 100vw;
    height: 10vh;
  }

  .split-right {
    width: 100%;
    max-width: 100%;
    justify-content: center;
  }
  
  .nameOutput {
    width: 95%;
    margin: 5px;
  }
}
